* {
  margin: 0;
  padding: 0;
}

body {
  font-family: Seymour One, sans-serif;
}

a {
  text-decoration: none;
}

.input-form {
  background-color: #b9d687;
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
  padding: 10px 0;
  display: flex;
}

input {
  width: 300px;
  height: 10px;
  border-color: #a9a9a9;
  margin-left: 10px;
  margin-right: 10px;
  padding: 10px;
  position: relative;
}

.search-button {
  cursor: pointer;
  height: 25px;
  width: 25px;
  color: #000;
  background-color: #a9a9a9;
  background-image: url("https://img.freepik.com/premium-vector/search-icon-magnifying-glass-symbol-outline-icon_543062-139.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border: none;
  position: absolute;
  top: 3%;
  right: 40%;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  display: flex;
}

.photo-card {
  box-shadow: 0 0 5px rgba(0, 0, 0, .3);
}

.info {
  width: 300px;
  gap: 15px;
  display: flex;
}

.info-item {
  text-align: center;
  color: #585757;
  margin-bottom: 0;
  font-size: small;
  text-decoration: none;
  display: inline-block;
}

.load-more {
  width: 80px;
  height: 30px;
  color: #556b2f;
  background-color: #b9d687;
  border-color: #696969;
  border-radius: 15px;
  margin: 20px;
  display: block;
}

